import manager from '@/store/manager.js'

class Homepage {
  constructor (data) {
    this._id = data._id
    this.domain = data.domain || null
    this.theme = data.theme || 'theme01'
    this.template = data.template || null
    this.pages = data.pages || null
  }
  get isDuplicate () {
    return manager.homepageService.homepages.some(one => {
      return one._id !== this._id && one.domain === this.domain
    })
  }
  recommend () {
    if (!this.domain) this.domain = manager.user.homepage
    if (!manager.user.vision) manager.user.vision = {}
    if (!manager.user.vision.title) manager.user.vision.title = ''
    if (!manager.user.vision.description) manager.user.vision.description = ''
    if (!manager.user.services) {
      manager.user.services = []
      manager.user.services.push(manager.const.service['buy'])
      manager.user.services.push(manager.const.service['rent'])
    }
  }
  toJSON () {
    return {
      _id: this._id,
      domain: this.domain,
      theme: this.theme,
      template: this.template,
      pages: this.pages
    }
  }
}

export default Homepage



